* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
  border: 1px solid #111;
  max-width: 200px;
  text-align: center;
  font-size: 18px;
}
.list_item {
  max-width: 700px;
  display: flex;
  justify-content: space-around;
}

.items div {
  border-top: 1px solid #111;
  text-align: center;
}
.items div h1 {
  margin-left: 1rem;
}
